import createMessageBody from '../utils/createMessageBody';

// –∫–ª–∞—Å—Å –¥–ª—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ –±–æ—Ç–æ–º –≤—Ö–æ–¥—è—â–∏—Ö –∫–æ–º–∞–Ω–¥

export default class Chatbot {
  constructor(botname, data, server) {
    this.botname = botname;
    this.description = `–ü—Ä–∏–≤–µ—Ç! –ú–µ–Ω—è –∑–æ–≤—É—Ç ${botname}. –ï—Å–ª–∏ —Ç—ã –∑–∞—Å–∫—É—á–∞–ª, —è –ø–æ–º–æ–≥—É —Ç–µ–±–µ –≤—ã–±—Ä–∞—Ç—å –∫–∞–∫–æ–π-–Ω–∏–±—É–¥—å –∑–∞—Ä—É–±–µ–∂–Ω—ã–π —Å–µ—Ä–∏–∞–ª –∏–ª–∏ —Ñ–∏–ª—å–º –∏ –¥–∞–∂–µ –∏–≥—Ä—É –Ω–∞ –ü–ö. –ß—Ç–æ–±—ã –æ–±—Ä–∞—Ç–∏—Ç—å—Å—è –∫–æ –º–Ω–µ, –Ω–∞—á–Ω–∏ —Å–≤–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ —Å–æ —Å–ª—ç—à–∞ ("/") –∏ –Ω–∞–ø–∏—à–∏ –æ–¥–Ω—É –∏–∑ –∫–æ–º–∞–Ω–¥: "hello", "bye", "serial", "film", "game". –ù–∞–ø—Ä–∏–º–µ—Ä: "/serial".`;
    this.greeting = data?.greeting;
    this.goodbye = data?.goodbye;
    this.serials = data?.serials;
    this.games = data?.games;
    this.server = server;

    this.greet();
  }

  reply(request) {
    if (!/^\//.test(request)) return;
    let message;

    switch (request) {
      case '/hello':
        message = createMessageBody(
          'bot',
          [{ type: 'text', body: this.random(this.greeting) }],
          this.botname
        );
        this.server.send(message);
        break;

      case '/bye':
        message = createMessageBody(
          'bot',
          [{ type: 'text', body: this.random(this.goodbye) }],
          this.botname
        );
        this.server.send(message);
        break;

      case '/serial':
        message = createMessageBody(
          'bot',
          [{ type: 'text', body: this.random(this.serials) }],
          this.botname
        );
        this.server.send(message);
        break;

      case '/film':
        fetch('https://kinopoiskapiunofficial.tech/api/v2.2/films', {
          method: 'GET',
          headers: {
            'X-API-KEY': '2abdabef-3994-4107-8e8d-39dd2e2edcda',
            'Content-Type': 'application/json',
          },
        })
          .then((res) => res.json())
          .then((json) => {
            const film = this.random(json.items);
            message = createMessageBody(
              'bot',
              [
                { type: 'text', body: film.nameOriginal },
                { type: 'image', body: film.posterUrlPreview },
              ],
              this.botname
            );
            this.server.send(message);
          })
          .catch((err) => {
            throw new Error(err);
          });

        break;

      case '/game':
        message = createMessageBody(
          'bot',
          [{ type: 'text', body: this.random(this.games) }],
          this.botname
        );
        this.server.send(message);
        break;

      default:
        message = createMessageBody(
          'bot',
          [
            {
              type: 'text',
              body: '–ö —Å–æ–∂–∞–ª–µ–Ω–∏—é, –¥–∞–Ω–Ω–∞—è –∫–æ–º–∞–Ω–¥–∞ –ø–æ–∫–∞ —á—Ç–æ –Ω–µ –¥–æ—Å—Ç—É–ø–Ω–∞ –º–Ω–µ. –ü–æ–ø—Ä–æ–±—É–π –µ—â–µ —Ä–∞–∑ –Ω–∞–ø–∏—Å–∞—Ç—å —Ç–æ—á–Ω–µ–µ :)',
            },
          ],
          this.botname
        );

        this.server.send(message);
    }
  }

  greet() {
    const message = {
      greet: true,
      usertype: 'bot',
      botname: this.botname,
      content: [{ type: 'text', body: this.description }],
    };

    this.server.send(JSON.stringify(message));
  }

  random(arr) {
    const randomIndex = Math.floor(Math.random() * arr.length);
    return arr[randomIndex];
  }
}
